/* ðŸŽ¨ Balanced Purple CSS Variables - Rich but Not Bright */
:root {
  --primary-purple: #a78bfa;          /* Rich medium purple */
  --secondary-purple: #c4b5fd;        /* Medium light purple */
  --purple-tint: #e0e7ff;             /* Light purple tint */
  --white: #ffffff;                    /* Pure white */
  --text-dark: #1f2937;               /* Dark text */
  --text-light: #6b7280;              /* Light text */
  --border-gray: #e2e8f0;             /* Soft border */
  --bg-light: var(--very-light-purple);  /* Very light purple background */
  --light-gray: #f8fafc;              /* Very light gray */
  --shadow-lg: 0 10px 15px -3px rgba(167, 139, 250, 0.08), 0 4px 6px -2px rgba(0, 0, 0, 0.03);
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* ðŸŽ¨ Auth pages styling - Pure White Background */
.auth-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  /* âœ¨ Pure white background */
  background: var(--white);
  padding: 2rem;
}

.auth-card {
  background: var(--very-light-purple);  /* Very light purple for component cards */
  border-radius: 1rem;
  /* âœ¨ Subtle purple shadow instead of black */
  box-shadow: 0 20px 25px -5px rgba(167, 139, 250, 0.1), 0 10px 10px -5px rgba(167, 139, 250, 0.04);
  border: 1px solid var(--purple-tint);
  padding: 1rem;
  width: 100%;
  max-width: 400px;
}

.auth-header {
  text-align: center;
  margin-bottom: 2rem;
}

.auth-logo {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  margin-bottom: 1rem;
}

.logo-icon {
  width: 40px;
  height: 40px;
  background: linear-gradient(135deg, var(--primary-purple), var(--secondary-purple));
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
  font-weight: bold;
  font-size: 1.25rem;
}

.auth-title {
  font-size: 1.75rem;
  font-weight: 700;
  color: var(--text-dark);
  margin-bottom: 0.5rem;
}

.auth-subtitle {
  color: var(--text-light);
  font-size: 0.875rem;
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.auth-footer {
  text-align: center;
  margin-top: 1.5rem;
  padding-top: 1.5rem;
  border-top: 1px solid var(--border-gray);
}

.auth-link {
  color: var(--primary-purple);
  text-decoration: none;
  font-weight: 500;
}

.auth-link:hover {
  text-decoration: underline;
}

/* Modern floating animation for login page */
@keyframes float {
  0%, 100% {
    transform: translateY(0px) translateX(0px) rotate(0deg);
  }
  33% {
    transform: translateY(-20px) translateX(10px) rotate(1deg);
  }
  66% {
    transform: translateY(10px) translateX(-5px) rotate(-1deg);
  }
}

@keyframes floatSlow {
  0%, 100% {
    transform: translateY(0px) scale(1);
  }
  50% {
    transform: translateY(-15px) scale(1.05);
  }
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.8;
    transform: scale(1.02);
  }
}

/* Captcha styling */
.captcha-container {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background-color: var(--light-gray);
  border-radius: 0.5rem;
  border: 1px solid var(--border-gray);
}

.captcha-image {
  width: 120px;
  height: 40px;
  background-color: var(--white);
  border: 1px solid var(--border-gray);
  border-radius: 0.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Courier New', monospace;
  font-weight: bold;
  letter-spacing: 2px;
  color: var(--text-dark);
}

.refresh-captcha {
  background: none;
  border: none;
  color: var(--primary-purple);
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 0.25rem;
  transition: background-color 0.2s ease;
}

.refresh-captcha:hover {
  background-color: rgba(124, 58, 237, 0.1);
}

/* Button styles */
.btn {
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 500;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s ease;
  border: none;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

/* ðŸŽ¨ Lighter Purple Buttons with Better White Balance */
.btn-primary {
  background: var(--primary-purple);
  color: var(--white);
  border: 2px solid transparent;
}

.btn-primary:hover {
  background: var(--white);
  color: var(--primary-purple);
  border: 2px solid var(--primary-purple);
  box-shadow: 0 4px 12px rgba(167, 139, 250, 0.15);
}

.btn-secondary {
  background: var(--white);
  color: var(--primary-purple);
  border: 2px solid var(--purple-tint);
}

.btn-secondary:hover {
  background: var(--purple-tint);
  color: var(--primary-purple);
  border: 2px solid var(--primary-purple);
}

/* Animation for loading spinner */
@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.animate-spin {
  animation: spin 1s linear infinite;
} 